.SideDrawer{
    position: fixed;
    width: 280px;
    max-width: 70%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: white;
    padding: 32px 16px;
    box-sizing: border-box;
    transition: transform   0.3s ease-out; /* Para abrir y cerrar el cajon  */
}


@media (min-width: 500px) { /* para dispositivo que superan los 500 pixeles */
    .SideDrawer {
        display: none;

    }

}

.Open{
    transform: translateX(0);

}

.Close{
    transform: translateX(-100%);

}

.Logo{
    height: 11%;
    /* Para el responsive del celular. Darle un espacio a los botones de la imges hamburguesa. */
    margin-bottom: 32px;
}